#respond #commentform.load textarea#comment {
  font-size: 0;
}
#respond .quicktags-toolbar,
#respond .wp-editor-tools,
#respond .wp-editor-wrap,
#respond .wp-switch-editor {
  float: left;
  display: block;
  width: 100%;
}
#respond .mce-tinymce.mce-container.mce-panel {
  border-radius: 2px;
  box-sizing: border-box;
  border: 1px solid #bbb;
  display: block;
  width: 100%;
  float: left;
}
#respond .mce-toolbar-grp.mce-container.mce-panel {
  background: transparent;
}
#respond .mce-toolbar .mce-btn-group .mce-btn,
#respond .qt-dfw {
  padding: 0;
  margin: 0;
}
#respond .mce-toolbar .mce-btn button {
  padding: 1px;
  margin: 0;
}
#respond .mce-toolbar .mce-ico {
  font-size: 17px;
}
#respond .mce-toolbar .mce-ico.mce-i-strikethrough {
  font-size: 23px;
}
#respond .mce-toolbar .mce-ico.mce-i-bullist {
  font-size: 20px;
}
#respond #comment_ifr {
  box-sizing: border-box;
  padding: 1px;
}
